// Utilitrios para exportao de relatrios

export const exportToPDF = (data, filename = 'relatorio.pdf') => {
  // Implementao bsica de exportao para PDF
  // Em uma implementao real, voc usaria uma biblioteca como jsPDF ou Puppeteer
  
  const printWindow = window.open('', '_blank');
  const htmlContent = generateHTMLReport(data);
  
  printWindow.document.write(htmlContent);
  printWindow.document.close();
  printWindow.print();
  
  // Simular download
  console.log(`Exportando relatrio para PDF: ${filename}`);
  alert(`Relatrio exportado para PDF: ${filename}`);
};

export const exportToExcel = (data, filename = 'relatorio.xlsx') => {
  // Implementao bsica de exportao para Excel
  // Em uma implementao real, voc usaria uma biblioteca como SheetJS
  
  const csvContent = generateCSVReport(data);
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const link = document.createElement('a');
  
  if (link.download !== undefined) {
    const url = URL.createObjectURL(blob);
    link.setAttribute('href', url);
    link.setAttribute('download', filename.replace('.xlsx', '.csv'));
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }
  
  console.log(`Exportando relatrio para Excel: ${filename}`);
};

export const exportToImage = (elementId, filename = 'relatorio.png') => {
  // Implementao bsica de exportao para imagem
  // Em uma implementao real, voc usaria html2canvas ou similar
  
  const element = document.getElementById(elementId);
  if (!element) {
    console.error('Elemento no encontrado para exportao');
    return;
  }
  
  // Simular exportao de imagem
  console.log(`Exportando elemento ${elementId} para imagem: ${filename}`);
  alert(`Relatrio exportado para imagem: ${filename}`);
};

const generateHTMLReport = (data) => {
  const currentDate = new Date().toLocaleDateString('pt-BR');
  
  return `
    <!DOCTYPE html>
    <html>
    <head>
      <meta charset="UTF-8">
      <title>Relatrio de Oramentos - Geco</title>
      <style>
        body { font-family: Arial, sans-serif; margin: 20px; }
        .header { text-align: center; margin-bottom: 30px; }
        .stats { display: flex; justify-content: space-around; margin: 20px 0; }
        .stat-card { border: 1px solid #ddd; padding: 15px; text-align: center; }
        .table { width: 100%; border-collapse: collapse; margin: 20px 0; }
        .table th, .table td { border: 1px solid #ddd; padding: 8px; text-align: left; }
        .table th { background-color: #f2f2f2; }
        .footer { margin-top: 30px; text-align: center; color: #666; }
      </style>
    </head>
    <body>
      <div class="header">
        <h1>Relatrio de Oramentos</h1>
        <p>Gerado em: ${currentDate}</p>
      </div>
      
      <div class="stats">
        <div class="stat-card">
          <h3>Total de Oramentos</h3>
          <p>${data.totalCount}</p>
        </div>
        <div class="stat-card">
          <h3>Valor Total</h3>
          <p>R$ ${data.totalValue.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}</p>
        </div>
        <div class="stat-card">
          <h3>Valor Mdio</h3>
          <p>R$ ${data.averageValue.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}</p>
        </div>
      </div>
      
      <h2>Distribuio por Tipo</h2>
      <table class="table">
        <thead>
          <tr>
            <th>Tipo</th>
            <th>Quantidade</th>
            <th>Percentual</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>Materiais</td>
            <td>${data.materialsCount}</td>
            <td>${data.totalCount > 0 ? ((data.materialsCount / data.totalCount) * 100).toFixed(1) : 0}%</td>
          </tr>
          <tr>
            <td>Mo de Obra</td>
            <td>${data.laborCount}</td>
            <td>${data.totalCount > 0 ? ((data.laborCount / data.totalCount) * 100).toFixed(1) : 0}%</td>
          </tr>
          <tr>
            <td>Combinados</td>
            <td>${data.combinedCount}</td>
            <td>${data.totalCount > 0 ? ((data.combinedCount / data.totalCount) * 100).toFixed(1) : 0}%</td>
          </tr>
        </tbody>
      </table>
      
      <div class="footer">
        <p>Relatrio gerado pelo sistema Geco</p>
      </div>
    </body>
    </html>
  `;
};

const generateCSVReport = (data) => {
  const headers = ['Tipo', 'Quantidade', 'Percentual'];
  const rows = [
    ['Materiais', data.materialsCount, data.totalCount > 0 ? ((data.materialsCount / data.totalCount) * 100).toFixed(1) + '%' : '0%'],
    ['Mo de Obra', data.laborCount, data.totalCount > 0 ? ((data.laborCount / data.totalCount) * 100).toFixed(1) + '%' : '0%'],
    ['Combinados', data.combinedCount, data.totalCount > 0 ? ((data.combinedCount / data.totalCount) * 100).toFixed(1) + '%' : '0%']
  ];
  
  const csvContent = [headers, ...rows]
    .map(row => row.map(cell => `"${cell}"`).join(','))
    .join('\n');
  
  return csvContent;
};

export const shareReport = (data) => {
  // Implementao bsica de compartilhamento
  // Em uma implementao real, voc usaria Web Share API ou redes sociais
  
  const shareData = {
    title: 'Relatrio de Oramentos - Geco',
    text: `Relatrio com ${data.totalCount} oramentos totalizando R$ ${data.totalValue.toLocaleString('pt-BR', { minimumFractionDigits: 2 })}`,
    url: window.location.href
  };
  
  if (navigator.share) {
    navigator.share(shareData)
      .then(() => console.log('Relatrio compartilhado com sucesso'))
      .catch((error) => console.log('Erro ao compartilhar:', error));
  } else {
    // Fallback para navegadores que no suportam Web Share API
    const shareText = `${shareData.title}\n\n${shareData.text}\n\n${shareData.url}`;
    navigator.clipboard.writeText(shareText)
      .then(() => {
        alert('Link do relatrio copiado para a rea de transferncia!');
      })
      .catch(() => {
        alert('No foi possvel copiar o link. Tente novamente.');
      });
  }
};

